<template>
  <div class="page-box">
    <HeadTop title="我的团队"></HeadTop>
    <div class="my-team">
      <div class="top flex-middle">
        <div class="item flex-item">
          <div class="price">{{childrenLength}}</div>
          <div class="desc">直推地址总量(个)</div>
        </div>
        <div class="item flex-item">
          <div class="price">{{Number(total).toFixed(3)}}</div>
          <div class="desc">直推质押总量(ABDX)</div>
        </div>
      </div>
      <div class="list-box">

      </div>
        <div class="ul" v-for="item in list">
        <div class="li">
          <div class="value"><span class="key">直推地址:</span>{{item.address}}</div>
        </div>
        <div class="li">
          <div class="value"><span class="key">质押总量:</span>{{item.amount}} ABDX</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import { Toast } from 'vant';
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      abdxContract: null,
      tokenDecimls: 0,
      childrenLength: 0,
      total: 0,
      list: [],
      initContract: async () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.tokenDecimls = await data.abdxContract.methods.decimals().call()
        data.initData()
      },
      initData: () => {
        data.getUserList()
      },
      getUserList: async () => {
        let userInfo = await data.investContract.methods.getUserInfo(data.address).call()
        let children = userInfo.children
        let total = 0
        let list = []
        let toast = null;
        if(children.length > 0) {
         toast = Toast.loading({
        duration: 0,
        forbidClick: true,
        message: '加载中...',
        });
        }
        
        data.childrenLength = children.length
        for(let i = 0; i < children.length; i++) {
            console.log(111111)
          let childInfo = await data.investContract.methods.getUserInfo(children[i]).call()
          let amount = Number(childInfo.investAmmount/10 ** data.tokenDecimls).toFixed(3);
          total = Number(amount) + Number(total);
          console.log(total)
          let params = {
            address: children[i],
            amount: amount
          }
          list.push(params)
        }
        data.list = list
        data.total = total
        if(children.length > 0) {
          toast.clear();
        }
        
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
.my-team{
    padding: 12px 15px;
  .top{
     background: linear-gradient(90deg, #0670FE, #3E54EC);
     padding: 20px 15px;
     text-align: center;
     border-radius: 6px;
     margin-bottom: 12px;
     .item{
         .price{
            font-size: 20px;
            font-weight: 600;
            color: #fff;
            margin-bottom: 4px;
         }
         .desc{
           font-size: 13px;
           color: #C8D4FF;  
         }
     }
  }
}
.ul {
  margin-bottom: 12px;
  border-radius: 8px;
  background: #fff;
  padding: 16px 13px;
  &:last-child {
    margin-bottom: 0;
  }
  .li {
    display: flex;
    line-height: 1.3;
    padding-bottom: 10.5px;
    div {
      display: inline;
    }
    &:last-child {
      padding-bottom: 0;
    }

    .key {
      font-size: 14px;
      color: #666;
      flex-shrink: 0;
    }
    .value {
      font-size: 14px;
      color: #111;
      word-break: break-all;
    }
  }
}
</style>

